import './styles.css';

const cityWeatherData = [
	{ id: 1, city: 'New York', weather: 'Sunny', temperature: '72°F' },
	{ id: 2, city: 'Los Angeles', weather: 'Partly Cloudy', temperature: '68°F' },
	{ id: 3, city: 'Chicago', weather: 'Rainy', temperature: '55°F' },
	{ id: 4, city: 'Miami', weather: 'Thunderstorms', temperature: '80°F' },
	{ id: 5, city: 'San Francisco', weather: 'Foggy', temperature: '62°F' },
	{ id: 6, city: 'Boston', weather: 'Partly Cloudy', temperature: '70°F' },
	{ id: 7, city: 'Seattle', weather: 'Showers', temperature: '58°F' },
	{ id: 8, city: 'Austin', weather: 'Clear', temperature: '75°F' }
];
const DummyList = () => {
	return (
		<ul className='list'>
			{cityWeatherData.map(cityWeather => (
				<li key={cityWeather.id} className='list_item'>
					<h2>{cityWeather.city}</h2>
					<div className='weather-data'>
						<p>Weather: {cityWeather.weather}</p>
						<p>Temperature: {cityWeather.temperature}</p>
					</div>
				</li>
			))}
		</ul>
	);
};
export default DummyList;
